<script>
	
	import $ from "jquery"
	import Swiper from "../assets/js/swipe.js"	
	import axios from "axios"
	import {mapState} from "vuex"
	export default {
		
		mounted() {
			
			this.$axios.all([this.$axios.get("/api/home",{params:{_page:1,_limit:4,listname:"HomeBanner"}}),
			this.$axios.get("/api/home",{params:{_page:1,_limit:5,listname:"HomeList"}})
			]).then(this.$axios.spread((banner,list)=>{
				// this.banner = banner.data
				// this.list = list.data
				this.$store.commit("HOME_BANNER",banner.data)
				this.$store.commit("HOME_LIST",list.data)
			}))
			
		},
		updated() {
			new Swiper($('.m-slider')[0], {
			  auto: 2000,
			  continuous: true, 	
			  stopPropation: true,
			  callback: function(index, element) {
			    $('.slider-pagination .slider-pagination-item').removeClass('slider-pagination-item-active');
			    $('.slider-pagination .slider-pagination-item')
			      .eq(index)
			      .addClass('slider-pagination-item-active');
			  }
			});
		},
		computed:{
			...mapState(["banner"]),
			...mapState(["list"])
		}
	}
