<template>
  <div id="app">
	  <Header v-show="navH"/>
	  <router-view></router-view>
	  <Footer v-show="navF"/>
  </div>
</template>

<script>
import Header from "./components/header.vue"
import Footer from "./components/footer.vue"
import {mapState} from "vuex"
export default {
  name: 'App',
  data:function(){
	  return {
		  Header,Footer
	  }
  },
  watch:{
	  $route:{
		  handler(nextValue){
			  let path = nextValue.path;
			  if(/home/.test(path)){
				  this.$store.commit('navH',true)
				  this.$store.commit('navF',true)
			  }
			  else if(/login|reg|set|detail/.test(path)){
				  this.$store.commit('navH',false)
				  this.$store.commit('navF',false)
			  }
			  else if(/me|video/.test(path)){
				  this.$store.commit('navH',false)
				  this.$store.commit('navF',true)
			  }
		  },
		  immediate:true
	  }
  },
  components: {
	  Header,Footer
  },
  updated(){
	  	
  },
  computed:{
	  ...mapState(['navH']),
	  ...mapState(['navF'])
  },
  created() {
	 let token = window.localStorage.getItem('user')
	 token = token ? JSON.parse(token):''
  	this.$store.commit(	'UPDATE_USER',token)
  }
}
</script>

