import Vue from "vue"
import axios from "axios"
import router from "./routes.js"
import store from "./vuex.js"
//添加一个请求拦截
axios.interceptors.request.use(function(config){
	//设置请求默认参数
	config.params = config.params || {
		_page:1,
		_limit:5
	}
	//获取token
	// let local = window.localStorage.getItem("user")
	let token =	store.state.token.token
	// let token = local?JSON.parse(local).token:""
	token = token ? token:""
	config.headers = {
		token
	}
	return config //返回配置后的请求
},function(error){
	//请求失败
	return Promise.reject(error)
});

//响应拦截
axios.interceptors.response.use(function(response){
	
	let path = router.currentRoute.fullPath
	if(response.data.err ==2 && !path.includes("/login")){
		// token校验失败 
		// 跳转
		router.replace({
			path:'/login',
			query:{
				backpath:path //发送当前位置给登录组件
			}
		})
		
	}
	return response.data
},function(error){
	return Promise.reject(error)
})
Vue.prototype.$axios = axios; //绑定到Vue原型未来通过this.$axios调用

export default axios 