let mutations = {
	HOME_BANNER(state,payload){
        state.banner = payload
    },
    HOME_LIST(state,payload){
        state.list = payload
    },
    ME_USER(state,payload){
        state.user = payload
    },
    navH(state,payload){
        state.navH = payload
    },
    navF(state,payload){
        state.navF = payload
    },
	UPDATE_USER(state,payload){
		state.token = payload
	}
}
export default mutations